import React from 'react';
import Image from "../../components/defaults/image"

const Year18 = () => {
    const images = [
        {
            name: "img20181",
            width: 600,
            height: 420,
        },
        {
            name: "img20182",
            width: 590,
            height: 420,
        },
        {
            name: "img20183",
            width: 580,
            height: 420,
        },
        {
            name: "img20184",
            width: 425,
            height: 420,
        },
        {
            name: "img20185",
            width: 590,
            height: 420,
        },
        {
            name: "img20186",
            width: 610,
            height: 420,
        },
    ]


    return (
        <div className="flex-between year18-album">
            {images.map((item, index) => <div className="year18-images" style={{ width: item.width }}><Image image={item.name} /></div>)}
        </div>
    );
}

export default Year18;